.me-container {
    display: grid;
    grid-template-columns: 70% 30%;
    gap: 20px;
    padding: 20px;
}

.projects {
    grid-column: 1 / 2;
}

.bio {
    grid-column: 2 / 3;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

/* Example sub-sections in the right column */
.education, .languages, .work {
    background-color: #f0f0f0;
    padding: 10px;
    border-radius: 8px;
}
